import React, { useEffect, useState } from "react";
import axios from "axios";

export default function MyOrders() {
  const [orders, setOrders] = useState([]);
  const [user, setUser] = useState(null);
  const [token, setToken] = useState(null);

  useEffect(() => {
    // Safe client-side token/user fetch
    const storedUser = localStorage.getItem("user");
    const storedToken = localStorage.getItem("token");
    if (storedUser) setUser(JSON.parse(storedUser));
    if (storedToken) setToken(storedToken);
  }, []);

  useEffect(() => {
    if (!user || !token) return; // wait until both are available

    axios
      .get(`http://localhost:5000/order/getbyuser/${user._id}`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((res) => setOrders(res.data))
      .catch((err) => console.error(err));
  }, [user, token]);

  const getStatusColor = (status) => {
    switch (status) {
      case "Pending": return "bg-gradient-to-r from-yellow-200 to-yellow-400 text-yellow-900";
      case "Paid": return "bg-gradient-to-r from-green-300 to-green-500 text-green-900";
      case "Shipped": return "bg-gradient-to-r from-blue-300 to-blue-500 text-blue-900";
      case "Delivered": return "bg-gradient-to-r from-emerald-300 to-emerald-500 text-emerald-900";
      case "Cancelled": return "bg-gradient-to-r from-red-300 to-red-500 text-red-900";
      default: return "bg-gradient-to-r from-gray-200 to-gray-400 text-gray-900";
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-100 via-slate-200 to-blue-100 px-6 py-12">
      <h1 className="text-4xl font-extrabold mb-12 text-slate-800 text-center tracking-tight">
        My Orders
      </h1>

      {orders.length === 0 ? (
        <p className="text-center text-slate-600 text-lg"> No orders yet.</p>
      ) : (
        <div className="space-y-10 max-w-5xl mx-auto">
          {orders.map((order) => (
            <div
              key={order._id}
              className="bg-white/70 backdrop-blur-xl rounded-3xl shadow-lg p-8 
                         hover:shadow-2xl hover:scale-[1.02] transition-all duration-300 border border-slate-200"
            >
              <div className="flex justify-between items-center mb-6">
                <h2 className="font-bold text-xl text-slate-800">
                  Order <span className="text-blue-600">#{order._id.slice(-6)}</span>
                </h2>
                <span
                  className={`px-4 py-1 rounded-full text-sm font-semibold shadow-sm ${getStatusColor(
                    order.status
                  )}`}
                >
                  {order.status}
                </span>
              </div>

              <div className="text-slate-700 space-y-2 mb-6">
                <p className="text-lg">
                  <span className="font-semibold"> Total Amount:</span> ₹{order.totalAmount}
                </p>
                <p className="text-sm text-slate-500">
                  {new Date(order.createdAt).toLocaleDateString()} • {" "}
                  {new Date(order.createdAt).toLocaleTimeString([], {
                    hour: "2-digit",
                    minute: "2-digit",
                  })}
                </p>
              </div>

              {order.items && order.items.length > 0 && (
                <div>
                  <h3 className="font-semibold text-slate-800 mb-3 text-sm uppercase tracking-wide">
                    Items in this order
                  </h3>
                  <ul className="space-y-2">
                    {order.items.map((item, idx) => (
                      <li
                        key={idx}
                        className="flex justify-between items-center bg-slate-50 px-4 py-3 
                                   rounded-xl shadow-sm border border-slate-100 text-sm"
                      >
                        <span> {item.productId?.name || "Product"}</span>
                        <span className="font-medium text-slate-700">Qty: {item.quantity}</span>
                      </li>
                    ))}
                  </ul>
                </div>
              )}
            </div>
          ))}
        </div>
      )}
    </div>
  );
}
